.App {
  --grey-100: #e2e1e1;
  --grey-200: #e9e9e9;
  --grey-300: #c4c4c4;
  --blue: rgb(0, 0, 194);
  --red: rgb(255, 37, 37);
  --green: rgb(69, 255, 23);
  --pink: rgb(255, 0, 234);
  --yellow: rgb(225, 255, 52);
  --orange: rgb(255, 138, 4);
  --cyan: rgb(0, 255, 221);
  --brown: rgb(131, 48, 15);
  text-align: center;
  background-image: url("../public/textured-paper.png");
  min-height: 100vh;
  padding: 3rem;
  color: var(--orange);
  font-family: "Inconsolata", monospace;
  font-family: "MedievalSharp", cursive;
}

h1 {
  font-size: 3rem;
  margin-top: 0;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2rem;
  margin: 1rem 0;
}

.selector,
.selected-players {
  &__players {
    list-style: none;
    padding-left: 0;
    display: grid;
    gap: 1rem;
    max-width: 50rem;
    margin: 0 auto;

    grid-template-columns: repeat(2, 1fr);

    @media (min-width: 768px) {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__player {
    background-color: var(--green);
    color: var(--pink);
    border: thin solid black;
    padding: 1rem;
    text-transform: capitalize;
    font-weight: bold;

    &:hover {
      cursor: pointer;
      background-color: var(--pink);
      color: var(--green);
    }
  }
}

.button {
  margin: 1rem auto;
  font-size: 2rem;
  text-transform: uppercase;
  padding: 1rem 2rem;
  background-color: var(--blue);
  color: var(--red);
  font-family: inherit;

  &:hover {
    cursor: pointer;
    background-color: var(--red);
    color: var(--blue);
  }
}

.best-matchup {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  flex-direction: column;

  @media (min-width: 768px) {
    flex-direction: row;
    gap: 2rem;
  }

  h3 {
    color: var(--cyan);
  }

  &__team {
    background-color: brown;
    padding: 0.5rem 1rem;
    width: 100%;

    @media (min-width: 768px) {
      width: 10rem;
      padding: 1rem 2rem;
    }

    ul {
      list-style: none;
      padding-left: 0;
    }
  }

  &__player {
    text-transform: capitalize;
    font-size: 1.5rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
  }

  &__vs {
    height: 3rem;
    width: 3rem;

    @media (min-width: 768px) {
      height: 8rem;
      width: 8rem;
    }
  }
}
